import { NgModule } from '@angular/core';
import { Routes, RouterModule, Route } from "@angular/router";
import { OtherDetailResolver } from '@app/modules/manage/reslovers/other-detail.reslover';
import { OtherQuestionResolver } from '@app/modules/manage/reslovers/otherQuestion.reslover';
import { QuestionTypeResolver } from '@app/modules/manage/reslovers/questions-type.reslover';
import { StudyPlansBoardComponent } from '@app/modules/manage/study-plans/study-plans-board/study-plans-board.component';
import { StudyPlansCreateComponent } from '@app/modules/manage/study-plans/study-plans-create/study-plans-create.component';
import { StudyPlansListComponent } from '@app/modules/manage/study-plans/study-plans-list/study-plans-list.component';
import { StudyPlansComponent } from '@app/modules/manage/study-plans/study-plans/study-plans.component';


const routes: Routes = [
  {
    path: "",
    component: StudyPlansComponent,
    resolve: {
      questionBank: OtherQuestionResolver
    },
    data: {
      type: 'study-plan'
    },
    children: [
      {
        path: "",
        component: StudyPlansBoardComponent,
      },
      {
        path: ":moduleName",
        component: StudyPlansListComponent,
      },
      {
        path: ":moduleName/create",
        component: StudyPlansCreateComponent,
        resolve: {
          questionType: QuestionTypeResolver,
        }
      },
      {
        path: ":moduleName/create/:detailId",
        component: StudyPlansCreateComponent,
        resolve: {
          questionType: QuestionTypeResolver,
          studyPlanDetail: OtherDetailResolver
        },
        data: {
          detailName: 'study-plan'
        },
      }
    ]
  },

];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  providers: [
    OtherQuestionResolver,
    QuestionTypeResolver,
    OtherDetailResolver
  ]
})

export class StudyPlansRoutingModule { }