import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CheckProgress } from '@app/shared/models/check-progress';

@Component({
  selector: 'app-study-plans-board',
  templateUrl: './study-plans-board.component.html',
  styleUrls: ['./study-plans-board.component.css']
})
export class StudyPlansBoardComponent implements OnInit {

  public questionBank:CheckProgress;

  constructor(private _activeRoute:ActivatedRoute,private _router:Router) { }

  ngOnInit() {
    this.questionBank=this._activeRoute.snapshot.data["questionBank"];
  }

  goToList(data){
    this._router.navigate([data.moduleType], { relativeTo: this._activeRoute });

  }

}
