import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { CommonService, SaveService, TableListService } from '@app/core/http/serviceIndex';
import { PredictionDetail,  } from '@app/shared/models/prediction';
import {PredictionFile} from '@app/shared/models/CommonModel'
import { TableFilter } from '@app/shared/models/table-filter';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-study-plans-create',
  templateUrl: './study-plans-create.component.html',
  styleUrls: ['./study-plans-create.component.css']
})
export class StudyPlansCreateComponent implements OnInit {
  public moduleName:string;
  
  createForm: FormGroup=this.formBuilder.group({
    name: ["", [Validators.required]],
    type: ["", [Validators.required]],
  });
  public dialog:any={
    showText:"",
    isDelete:false,
    isWarning:true,
    isconfirmBtn:false
  }
  public typeList: any;

  public tableFilter=<TableFilter>{
    page:1,
    limit:10,
    sortColumn:'createdDate',
    sortDirection:'desc',
    search:null
  };


  public selectedLists: any = [];
  public selectedQues: any = [];

  // any
  public studyPlanId: any;
  public checkType: any;


  // boolean
  public isStep1: boolean = true;
  public isAsc: boolean = true;
  public submitted: boolean = false;
  public isProgress: boolean = false;
  public isDialog: boolean = false;
  public subscription: Subscription;

  constructor(
    private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private _cservice: CommonService,
    private _createService:SaveService,
    private _tableService:TableListService
  ) { }

  ngOnInit() {
    this.moduleName=this._activeRoute.snapshot.paramMap.get("moduleName");
    this.studyPlanId = this._activeRoute.snapshot.paramMap.get("id");
    this.typeList=this._activeRoute.snapshot.data['questionType'].questions;

    let data = this._activeRoute.snapshot.data['studyPlanDetail'];
    if (data)
      this.getDataEditForm(data);
  }


  getDataEditForm(data: PredictionDetail) {
    this.studyPlanId=data.planId;

    this.createForm =  this.formBuilder.group({
      name: [data.name, [Validators.required]],
      type: [data.questionType, [Validators.required]],
    });
    this.selectedQues=data.questions;
  }



  goToStep2() {
    this.submitted = true;
    if (this.createForm.invalid) {
      return;
    } else {
      this.isStep1 = false;
      this.tableFilter={
        page:1,
        limit:10,
        sortColumn:'createdDate',
        sortDirection:'desc'
      };

      this.getSelectLists();
    }
  }

  backStep1() {
    this.isStep1 = true;
  }

  selectedData(e) {
    this.tableFilter.limit = e;
    this.getSelectLists();
  }

  searchDatas(e) {
    this.tableFilter.search = e;
    this.getSelectLists();
  }

  get f() {
    return this.createForm.controls;
  }

  getSelectLists() {
    this.selectedLists =null;
    let module={
      moduleType:this.moduleName,
      questionType:this.createForm.value.type
    };
    this._tableService
      .getManageQuestionList(
        this.tableFilter,module
      )
      .subscribe(
        (res) => {
          this.selectedLists = res.questions;
          this.tableFilter=res.filter;
        } );
  }

  selectfile(id) {
    let index = this.selectedQues.indexOf(id);
    if (this.selectedQues.includes(id)) {
      this.selectedQues.splice(index, 1);
    } else {
      this.selectedQues.push(id);
    }
  }

  onSave() {
    let obj = new PredictionFile();
    obj.name = this.createForm.value.name;
    obj.questionType = this.createForm.value.type;
    obj.questions = this.selectedQues;
    this._createService.onSaveStudyPlan(obj,this.moduleName,this.studyPlanId).subscribe((res) => {
      this.goBack();
    });
  }

  goBack() {
    this._cservice.goBackPrev();
  }

  changePage(e) {
    this.tableFilter.page = e;
    this.getSelectLists();
  }

  checkAvaiable() {
    if (this.selectedQues.length == 0) {
      this.isDialog = true;
      this.dialog.showText = 'Select Question!'
    } else {
      this.onSave();
    }
  }

  checkconfirm(e) {
    this.isDialog = e;
  }

  fitter(sortColumn) {
    this.isAsc = !this.isAsc ? this.isAsc = true : this.isAsc = false;
    this.tableFilter.sortDirection = this.isAsc ? 'asc' : 'desc';
    this.tableFilter.sortColumn = sortColumn;
    this.getSelectLists();
  }

  ngOnDestroy(){
    if(this.subscription)
    this.subscription.unsubscribe();
  }
}
